.CreateTodoButton {
    background-color: #00D7DB;
    box-shadow: 0px 0px 25px #00F4F8;
    border: none;
    border-radius: 50%;
    cursor: pointer;
    font-size: 50px;
    align-self: center;
    bottom: 24px;
    right: 24px;
    font-weight: bold;
    color: #FAFAFA;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 64px;
    width: 64px;

    transform: rotate(0);
    transition: .3s ease;
    /* z-index: 1; */
}

.CreateTodoButton:hover {
    transform: rotate(224deg);
}